export default {
  data() {
    return {
      posts: [],
      limit: 3,
    }
  },
  async fetch() {
    this.posts = await this.$http.$get(
      `/api/articles/p/${this.$route.query.page}/${
        this.$route.query.limit ? this.$route.query.limit : this.limit
      }`
    )
  },
  methods: {
    linkGen(pageNum) {
      return pageNum === 1 ? '?' : `?page=${pageNum}`
    },
  },
  watch: {
    '$route.query': '$fetch',
  },
}
